@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans KR",sans-serif;color:#333;letter-spacing:.5px}.container{width:100%;padding:0 20px;max-width:1024px;margin:0 auto}h3,h4{padding-bottom:5px;border-bottom:1px solid #ececec;letter-spacing:1px;color:#87bd5e}.period{background:#f9e7e6;font-size:.9rem;padding:0 4px;margin-left:10px}.title{padding-top:40px}.title>h3{font-size:1.6rem;font-weight:500}.title>h3>span{font-weight:400;padding-left:10px;color:#777}.box{padding:30px 0}.box>h4{margin:10px 0;font-size:1.3rem;font-weight:400}.box>p{padding-bottom:10px}.profile>p>span{display:inline-block}.profile>p>span:first-child{min-width:100px;font-weight:600}.career>p>span{background:#f9e7e6;font-size:.9rem;padding:0 4px;margin-left:10px}.project>div{padding:20px 0 50px 0}.project>div>div{position:relative;padding:20px 0 30px 0}.project>div>div>p:first-child{font-size:1.4rem;font-weight:200;padding-bottom:10px}.project>div>div>div{padding-top:15px}.project>div>div>div>img{margin-right:3px}@media screen and (max-width: 720px){*{font-size:14px}}@media screen and (min-width: 720px)and (max-width: 1024px){*{font-size:16px}}@media screen and (min-width: 1024px){*{font-size:18px}}
